      <h3 class="heading__main">popular apartments</h3>
    </div>
    <div class="popular-apartments__main">
      <div class="container">
        <div class="tab-panel">
          <div class="tabs">
            <div class="tab-active-overlay" :class="{'overlay-pos-left': overlayLeft, 'overlay-pos-right': overlayRight}"></div>
            <button class="tab-btn" :class="{'tab-active': overlayLeft}" @click="setActive('day')">Per Day</button>
            <button class="tab-btn" :class="{'tab-active': overlayRight}" @click="setActive('month')">Per Month</button>
          </div>
          <div class="tabs-content">
            <div class="tab-content" :class="{'active-tab-content':isActive('day')}">
              <div class="popular-apartments__wrap">

                <div class="popular-apartments__item">
                  <div class="popular-apartments__item-img" style="background-image: url('https://www.marinabaysands.com/content/dam/singapore/marinabaysands/master/main/home/hotel/rooms-suites/premier-room/premiere%201_1920x960.jpg')">
                    <div class="popular-apartments__item-hover"></div>
                    <div class="popular-apartments__item-info">
                      <ul>
                        <li>
                          <span>2 Bathrooms</span>
                        </li>
                        <li>
                          <span>2400 sqft</span>
                        </li>
                        <li>
                          <span>2 Bedrooms</span>
                        </li>
                        <li>
                          <span>__</span>
                        </li>
                        <li>
                          <p>Delta</p>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>

                <div class="popular-apartments__item">
                  <div class="popular-apartments__item-img" style="background-image: url('https://www.marinabaysands.com/content/dam/singapore/marinabaysands/master/main/home/hotel/rooms-suites/premier-room/premiere%201_1920x960.jpg')">
                    <div class="popular-apartments__item-hover"></div>
                    <div class="popular-apartments__item-info">
                      <ul>
                        <li>
                          <span>2 Bathrooms</span>
                        </li>
                        <li>
                          <span>2400 sqft</span>
                        </li>
                        <li>
                          <span>2 Bedrooms</span>
                        </li>
                        <li>
                          <span>__</span>
                        </li>
                        <li>
                          <p>Delta</p>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>

              </div>
            </div>
            <div class="tab-content" :class="{'active-tab-content':isActive('month')}">
  2
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>



  export default {
    data(){
      return{
        overlayLeft: true,
        overlayRight: false,
        activeTab: 'day',
        itemInfoOpen: false
      }
    },
    methods:{
      setActive(tabName){
        this.activeTab = tabName;
        if(this.activeTab === 'day'){
          this.overlayLeft = true;
          this.overlayRight = false;
        } else{
          this.overlayLeft = false;
          this.overlayRight = true;
        }
      },
      isActive(tabName){
        return this.activeTab === tabName;
      }
    }
  }
</script>

<style lang="scss">
.popular-apartments{
  &__main{
    .tab-panel{
      text-align: center;
      .tabs{
        position: relative;
        border: 1px solid #e5e5e5;
        -webkit-border-radius: 4px;
        -moz-border-radius: 4px;
        border-radius: 4px;
        overflow: hidden;
        display: inline-block;
        .tab-active-overlay{
          position: absolute;
          width: 150px;
          height: 50px;
          -webkit-transition: .5s;
          -moz-transition: .5s;
          -ms-transition: .5s;
          -o-transition: .5s;
          transition: .5s;
          -webkit-border-radius: 4px;
          -moz-border-radius: 4px;
          border-radius: 4px;
          background: #4588f2;
          &.overlay-pos-left{
            left: 0;
          }
          &.overlay-pos-right{
            left: 150px;
          }
        }
        .tab-btn{
          position: relative;
          width: 150px;
          height: 50px;
          border: none;
          outline: none;
          font-weight: 500;
          background: transparent;
          &.tab-active{
            color: #fff;
          }
        }
      }
      .tabs-content{
        .tab-content{
          display: none;
          &.active-tab-content{
            display: block;
          }
        }
      }
    }
  }
  &__wrap{
    display: flex;
    flex-wrap: wrap;
  }
  &__item{
    width: 33%;
    padding: 10px;
    &-img{
      position: relative;
      height: 300px;
      -webkit-border-radius: 4px;
      -moz-border-radius: 4px;
      border-radius: 4px;
      overflow: hidden;
      background: #e6e9ed;
      background-position: center;
      background-size: cover;
    }
    &-hover{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      -webkit-transition: .2s;
      -moz-transition: .2s;
      -ms-transition: .2s;
      -o-transition: .2s;
      transition: .2s;
      z-index: 1;
      background: rgba(0,0,0,.6);
      &:hover{
        opacity: 1;
      }
      &:hover + .popular-apartments__item-info{
        display: block;
      }
    }
    &-info{
      position: absolute;
      bottom: 0;
      display: none;
      li{
        opacity: 0;
        -webkit-animation: fadeIn 0.9s 1;
        animation: fadeIn 0.9s 1;
        -webkit-animation-fill-mode: forwards;
        animation-fill-mode: forwards;
        &:nth-child(1){
          -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
        }
        &:nth-child(2){
          -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
        }
        &:nth-child(3){
          -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
        }
      }
    }
  }
}
.item-info-open{
  display: block;
}

/* Animation steps */

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0.0;
  }
  100% {
    opacity: 1.0;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0.0;
  }
  100% {
    opacity: 1.0;
  }
}

</style>